/* src/components/StickyNote.css */
.sticky-note {
    width: 200px;
    padding: 20px;
    background-color: #ffeb3b;
    border: 1px solid #ccc;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.2);
    border-radius: 8px;
    margin: 10px;
    font-family: Arial, sans-serif;
  }
  
  .sticky-note-content {
    margin: 0;
    font-size: 16px;
    color: #555;
  }