/* src/App.css */
.body {
  text-align: center;
  padding: 20px;
}

.notes_container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
}

.note_form {
  margin-top: 20px;
}

.note_form input,
.note_form textarea {
  display: block;
  width: 300px;
  margin: 10px auto;
  padding: 10px;
  font-size: 16px;
}

.note_form button {
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  background-color: #2196f3;
  color: white;
  border: none;
  border-radius: 5px;
}

.note_form button:hover {
  background-color: #1976d2;
}

.menu {
  position: absolute;
  background-color: #fff;
  border: 1px solid #ddd;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
  border-radius: 4px;
  padding: 7px;
  margin-top: 7px;
  z-index: 100;
  font-size: small;
}

.menu ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.menu li {
  padding: 5px 10px;
  cursor: pointer;
}

.menu li:hover {
  background-color: #f0f0f0;
}